<template>
  <div class="hello">
      <button @click="testDatabase()">Test database</button>
      <div class="row">
          <div class="col-6">
              <div class="m-2">
                  <img width="400px" height="400px"
                       v-if="inputImage.fullImageBase64" v-bind:src="inputImage.fullImageBase64"/>
                  <img width="400px" height="400px"
                       v-else v-bind:src="imageNoPhoto"/>
              </div>
              <div>
                  <button @click="getTestImageSki()">Load image from backend</button>
              </div>
          </div>
          <div class="col-6">
              <div class="m-2">
                  <img width="400px" height="400px"
                       v-if="outputImage.fullImageBase64" v-bind:src="outputImage.fullImageBase64"/>
                  <img width="400px" height="400px"
                       v-else v-bind:src="imageNoPhoto"/>
              </div>
              <div>
                  <button @click="transformToGrayscale()">Convert to grayscale</button>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
    import imageApi from './api/image-api.js'

export default {
  name: 'hello',
  data () {
    return {
      inputImage: {
        fullImageBase64: null,  //TODO change to typescript class ??
        base64: null,
        type: null
      },
      outputImageName: null,
      outputImage: {
        fullImageBase64: null,
        base64: null,
        type: null
      },
      imageNoPhoto: 'data:inputImage/png;base64,'
    }
  },
  methods: {
    getTestImageSki() {
      imageApi.getTestImageSki()
        .then(response => {
          this.inputImage = {
            fullImageBase64: 'data:inputImage/' + response.data.type +  ';base64,' + response.data.base64,
            base64: response.data.base64,
            type: response.data.type
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    waitForCompletedAction() {
      const completedAction = new EventSource('/api/sse/completed-action');
      const objectContext = this;

      completedAction.onmessage = function(event) {
        const eventData = JSON.parse(event.data);

        imageApi.getImage(objectContext.outputImageName)
          .then(response => {
            objectContext.outputImage = {
              fullImageBase64: 'data:inputImage/' + response.data.type +  ';base64,' + response.data.base64,
              base64: response.data.base64,
              type: response.data.type
            }
          })
          .catch(error => {
            console.log(error)
          });
      }
    },
    transformToGrayscale() {
      this.waitForCompletedAction();
      imageApi.transformToGrayscale(this.inputImage)
        .then(response => {
          console.log('grayscale', response.data);
          this.outputImageName = response.data;
        })
        .catch(error => {
          console.log(error)
        })
    },
    testDatabase() {
      imageApi.testDatabase().then(response => {
        console.log(response);
      })
    }
  },
  created() {
    //this.waitForCompletedAction(); //TODO check performance
  }
}

</script>

